.profile-container {
  padding: 3rem 6rem;
}

.profile-header h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.profile-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 4rem;
  flex-wrap: wrap;
}

.profile-left {
  flex: 1;
  min-width: 350px;
}

.form-group {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.form-group input {
  padding: 0.6rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: #f9f9f9;
  font-size: 1rem;
}

.profile-right {
  flex: 0 0 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-right img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #ddd;
}

.profile-placeholder {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 2px solid #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: #777;
}

.loading {
  text-align: center;
  margin-top: 3rem;
  font-style: italic;
  color: #666;
}
